
 @media (min-width:200px){
  html{
    height: 698px;
  }
  .container{
    background-color: rgb(14, 43, 77);
    padding: 4rem;
    min-height: 430px;
    } 
    .container .slider {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: -3.3rem;
    }
    
    .container .btn {
      z-index: 1000;
      margin-right: -22%;
      font-size: 5rem;
      background-color: #fdfdfd00;
      border: none;
      color: #07519170;
      margin-left: -22%;
    }
    .container video {
      width: 150%;
      height: auto;
    } 
  }
  @media (min-width:350px){
    .container video {
      width: 130%;
    }
    .container .btn {
      margin-right: -13%;
      font-size: 4rem;
      margin-left: -13%;
    }
  }
  @media (min-width:480px){
    .container video {
      width: 120%;
    } 
    .container .btn {
      margin-right: -7%;
      font-size: 4rem;
      margin-left: -7%;
    }
  }
  @media (min-width:768px){
    .container video {
      width: 113%;
    } 
    .container .btn {
      margin-right: -6%;
      font-size: 5rem;
      margin-left: -6%;
    }
  } 
  @media (min-width:1024px){
    .container video {
      width: 75%;
    } 
    .container .btn {
      margin-right: -4%;
      font-size: 5rem;
      margin-left: -4%;
    }
  }