@media (min-width:200px){
    .container{
        display: block;
        justify-content: space-between;
    }
    .centra{
        width: 100%;
        background-color:#023059;
        padding: 2rem;
        padding-top: 7%;
       text-align: center;
    }
    .texCentral{
        width: 100%;
        background-color: #dcdee2;
        padding: 1rem;
    }
    .container .texCentral .fundo{
        background-color: #f2e205a6;
        padding: 2rem;
       border-radius: 50%;
       width: 95%;
       margin: 0px 1rem;
    }
  
    .container .texCentral img{
        width: 43%;
        margin-left: 27%;
    }
    .container .texCentral h1{
       font-size: 1.3rem;
    }
    .centra label{
        font-family: Arial, Helvetica, sans-serif;
       color: #F2E205;
       text-shadow: 1px 1px 2px rgba(10, 10, 10, 0.178);
       font-size: .8rem;
       font-weight: 600;
       text-align: center;
    }
    .container .texCentral h4{
        font-family: sans-serif;
        margin-top: 0px;
        font-size: 3rem;
        color: #023059;
        text-align: center;
    }
    
    .cont .form form{
        margin-top: 17%;
        width: 100%;
    }
    .cont .form form input{
        width: 40%;
        border-radius: 2rem;
        text-align: center;
        border: .2px solid rgba(0, 0, 0, 0.281);
    }
    .cont .form form textarea{
        width: 40%;
    } 
}
@media (min-width:480px){
    .container .texCentral h4{
        margin-bottom: 2rem;
        margin-top: 1rem;
        font-size: 2.9rem;
    }
    .container .texCentral h1{
        font-size: 1.9rem;
     }
}
@media (min-width:768px){
    .centra{
        width: 70%;
        background-color:#023059;
        padding: 2rem;
        padding-top: 7%;
       text-align: center;
    }
    .container{
        display: flex;
        justify-content: space-between;
    }
    .container .texCentral h4{
        margin-bottom: 2rem;
        font-size: 3.5rem;
    }
    .container .texCentral h1{
        font-size: 1.9rem;
     }
}
@media (min-width:1024px){
    .texCentral span:hover{
       font-size: 3rem;
    }
    .container .texCentral h4{
        margin-bottom: 1rem;
        font-size: 2.6rem;
    }
}
