@media (min-width:200px){
    .aside{
        display: block;
    }
    .aside header{
        font-size: 1rem;
    }
    .aside header:hover{
        font-size: 1.1rem;
    }
    .aside nav{
        padding: 2px; 
        border-radius: 3px;
    }
    .fa{
        font-size: .9rem;
    }  
    .fa:hover{
        font-size: 1rem;
        padding: 2px 3px;
        border-radius: 2px;
}
}
@media (min-width:768px){
    .aside{
        display: none;
    } 
}
.aside{
    cursor: pointer;
}
.aside header{
    font-size: 1rem;
}
.aside header:hover{
    color: #bdbcbc;
    font-size: 1.1rem;
}
.aside{
    background-color: rgba(0, 0, 0, 0.301);
    padding: 1rem;
}
.aside nav{
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.fa{
    font-family: sans-serif;
    padding: .5rem;
    margin-right: -1rem;
    font-weight: 600;
    color: #fff;
    text-decoration: none;
    transition: .5s;
}  
.fa:hover{
    color: #fbff01; 
    background-color: rgba(127, 251, 255, 0.301);
    border-bottom: 1px solid #fbff01;
}

