
@media (min-width:200px){
  body{
    max-height: 1000px;
  }
    .carousel {
        position: relative;
        width: 100%;
        overflow: hidden;
        z-index: 0;
        height: 200px;
    }
      .slides {
        width: 1100%;
        display: flex;
        transition: transform 3s ease-in-out;
        z-index: 0;
        height: 200px;
      }
      .slide {
        flex: 1;
        background-size: contain;
        background-repeat: no-repeat;
       z-index: 0;
       height: 200px;
      }
      .c1{
        display: none;
      }
      .slide.active {
        opacity: 1;
        background-size: contain;
        background-repeat: no-repeat;
        height: 200px;
      }
      
      .controls {
        bottom: 100px;
        display: none;
        justify-content: center;
        align-items: center;
        background-color: #0068ff21;
        padding: 1rem;
      }
      .controls2 {
        bottom: 100px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #0068ff21;
        padding: .4rem;
        margin-top: -0%;
        z-index: 1000;
      }
     
      button {
        margin: 0 10px;
        border: none;
        background-color: transparent;
        color:#256093;
        font-size: 1rem;
        cursor: pointer;
        transition: color 0.7s ease-in-out;
      }
      
      button.active,
      button:hover {
        color: var(--segunda);
      }
    .centra{
        width: 1349px;
        overflow: hidden;
        transition: 2s;
      
    }
    .centra .container{
        display: flex;
        transition: 2s; 
        justify-content: space-between; 
    }
    .img{
        background-size:contain;
        transition: 2s;
    }
    .container{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        transition: 2s;
        z-index: 2000;
    }
    .container2{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        transition: 2s;
        z-index: 2000;
    }
    
}
@media (min-width:270px){
    .controls2 {
        margin-top: 0%;
      }
}
@media (min-width:350px){
 
    .controls2 {
        margin-top: 0%;
      }
}
@media (min-width:480px){
    .controls2 {
        margin-top: 0%;
      }
      .container2{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        transition: 2s;
    }
}
@media (min-width:630px){
  .controls2 {
    margin-top: 0%;
  }
}
@media (min-width:768px){
  .slides {
    height: 400px;
  }
  .slide {
   height: 400px;
  }
  .slide.active {
    height: 400px;
  }
  .c1{
    display:block;
  }
  .c2{
    display:none;
  }
    .controls2 {
        display: none;
      }
      .controls {
        display: flex;
        margin-top: 7%;
      }
      button {
        font-size: 1.2rem;
      }
}
@media (min-width:1024px){
  .controls {
    margin-top: 0%;
  }
}
