@media (min-width:200px){
.nav h1{
    font-size: 1.5rem; 
}
.nav p{
    font-size: .7rem;
}
.nav div img{
    width: 10%;
}
.nav .div{
    display: none;
}
.rep{
    display: none;
}
}
@media (min-width:480px){
    
}
@media (min-width:768px){
    .nav{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-between;
    align-items: center;
    padding: 1px 1px;
    background-color: var(--principal);
    color:azure;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.596);
    position: sticky;
    z-index: 1000;
    top: 0;
}
    .rep{
        display: block;
    }
    .nav .div{
        display: flex;
    }
    .nav h1{
        font-size: 2.7rem; 
    }
    .nav p{
        font-size: .7rem;
    }
    .nav div img{
        width: 13%;
    }
    .nav .div a{
        font-size: 1.3rem;
        font-weight: 600;
        margin-right: 20px;
        padding: .8rem;
     }
     .nav .div a:hover{
         border-radius: 5px;
         padding: .9rem;
     }
}
@media (min-width:1024px){
   
}
.nav{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-between;
    align-items: center;
    padding: 1px 1px;
    background-color: var(--principal);
    color:azure;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.596);
    position: sticky;
    z-index: 1000;
    top: 0;
}
.nav h1{
    font-weight: bold;
    font-family: sans-serif;
    font-size: 2.2rem;
}
.nav .div{
    margin-left: -38%;  
}
.nav .divlogo{
    display: flex;
}
.nav .div a{
    font-family: Arial, Helvetica, sans-serif;
   text-decoration: none;
   color: aliceblue;
   transition: .6s;
   cursor: pointer;
}
.nav .div a:hover{
    background-color: #EEE400;
    color: #035AA6;
}
.active{
    background-color: #EEE400;
    color: #035AA6;
    border-radius: 3px; 
}
.alinha{
    margin-block: auto;
}
.nav p{
    text-align: center;
    font-size: .7rem;
    font-family: sans-serif;
}
.fa{
    display: none;
}
