.card{
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px solid #119fbfd3;
    border-radius: 10px;
    margin: 10px;
}
.card:hover{
    background-color: #119fbf3f;
    padding: 10px;
}

.card .h2{
    font-size: 1.8rem;
    color: #023059;
    text-align: center;
    padding: 10px;
}
.ico{
    font-size: 4rem;
    color: #F2A413;  
}
.card .p{
    font-size: 1.1rem;
    color: #023059;
    padding: 10px;
    text-align: center;
}
@media (min-width:200px){
    .card{
        width: 49%;
        height: 420px;
        border-radius: 4px;
        margin: 6px;
    }
    .card:hover{
        padding: 5px;
    }
    
    .card .h2{
        font-size: 1.3rem;
        padding: 5px;
    }
    .card svg{
        font-size: 3rem;
       margin-top: 2rem;
    }
    .card .p{
        font-size: .9rem;
    } 
}
@media (min-width:350px){
    .card{
        height: 382px;
    }
}
@media (min-width:480px){
    .card{
        border-radius: 6px;
        margin: 7px;
        width: 50%;
        height: 395px;
    }
    .card:hover{
        padding: 7px;
    }
    
    .card .h2{
        font-size: 1.6rem;
        padding: 7px;
    }
    .card svg{
        font-size: 5rem;
        margin-top: 2rem;
    }
    .card .p{
        font-size: 1rem;
    } 
}
@media (min-width:768px){
    .card{
        border-radius: 8px;
        margin: 8px;
        width: 33%;
        height: 465px;
    }
    .card:hover{
        padding: 7px;
    }
    
    .card .h2{
        font-size: 1.8rem;
        padding: 8px;
    }
    .card svg{
        font-size: 6rem;
       
    }
    .card .p{
        font-size: 1.1rem;
    } 
} 
@media (min-width:1024px){
    .card{
        height: 395px;
    }
    .card .h2{
        font-size: 2rem;
        padding: 10px;
    }
    .ico{
        font-size: 4rem;
        color: #F2A413;  
    }
    .card .p{
        font-size: 1.1rem;
        color: #023059;
        padding: 10px;
        text-align: center;
    }
}