


@media (min-width:200px){
    .footer{
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        align-content: center;
        justify-content: center;
        align-items: center;
        padding: 2rem;
        background-color: var(--principal);
        color:azure;
       
    }
    .footer a{
        text-decoration: none;
        font-size: 1.1rem;
        font-weight: bold;
        color:var(--segunda);
        cursor: pointer;
        font-family: sans-serif;
        margin: 0px 2rem;
    }.footer a:hover{
        font-size: 1.3rem;
    }
    .footer li{
        font-family: sans-serif;
        font-size: .9rem;
        padding: .2rem;
    }
    }
 @media (min-width:480px){
    .footer li{
        font-family: sans-serif;
        font-size: 1rem;
        padding: .2rem;
    }  
    }
@media (min-width:768px){
    .footer li{
        font-family: sans-serif;
        font-size: 1.rem;
        padding: .2rem;
    }
    }
@media (min-width:1024px){
    .footer li{
        font-family: sans-serif;
        font-size: 1..2rem;
        padding: .2rem;
    }
    }
    