
@media (min-width:200px){
    .posiçao{
        position: fixed;
        bottom: 5px;
        right: 3px;
        cursor: pointer;
    }
    .posiçao a{
        font-family: Arial, Helvetica, sans-serif;
        text-decoration: none;
        font-size: .9rem;
    }
    .posiçao a:hover{
        font-family: Arial, Helvetica, sans-serif;
        text-decoration: none;
        font-size: 1rem;
    }
    .posiçao a div{
        display: block;
        background-color: rgba(240, 248, 255, 0.363);
        color: black;
        padding: 5px;
        text-align: center;
        border-radius: 2px ;
    }
    .posiçao a div h3{
        color: black;
        text-align: center;
        font-size: .7rem;
    }
    .posiçao a div p{
      color: rgb(9, 168, 30);
      text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.466);  
      font-size: .7rem;
    }
    .posiçao svg{
     font-size: 2.7rem; 
     color: azure;  
     background-color: rgb(9, 168, 30);
     border-radius: 15%;
     box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.315);
     transition: .5s;
     margin-top: 8px;
    }
}
@media (min-width:480px){
    .posiçao a{
        font-size: .9rem;
    }
    .posiçao a:hover{
        font-size: 1rem;
    }
    .posiçao a div{
        padding: 5px;
        border-radius: 2px ;
    }
    .posiçao a div h3{
        font-size: 1rem;
    }
    .posiçao a div p{
      font-size: 1rem;
    }
    .posiçao svg{
     font-size: 3rem; 
     border-radius: 15%;
     margin-top: 8px;
    }     
}
