
@media (min-width:200px){
    .cart{
        display: block;
        background-color: #003a52;
        margin: 2px;
    }
    .cart div h3{
        color: white; 
        font-size: 1.5rem; 
        text-align: center;
        padding: 5px;
    }
    .cart div p{
        color: white; 
        font-size: .8rem; 
        text-align: center;
        padding: 5px;
        width: 85%;
        margin: auto;
    }
    .fund{
        background-color: var(--letra2);
        border-radius: 100px;
        margin: 10px 10px;
        width: 100%;
        box-shadow: 5px 5px 7px rgba(0, 0, 0, 0.329);
    }
    .cart div{
        width: 97%;
    }
    .imge{
        width: 100%;
    }
    .img{
        width: 65%;
        margin-left: 16%;
    }
    .fond{
        width: 100%;
        border-radius: 100px;
        margin: 10px 5px;
        background-size: contain;
        box-shadow: 5px 5px 7px var(--letra2);
    }
}
@media (min-width:480px){
    .cart{
        margin: 15px;
    }
    .cart div h3{
        font-size: 1.7rem; 
        padding: 5px;
    }
    .cart div p{
        font-size: 1rem; 
        padding: 5px;
        width: 80%;
        margin: auto;
    }
    .fund{
        margin: 10px 10px;
        width: 50%;
    }
    .cart div{
        width: 98%;
    }
    .imge{
        width: 90%;
    }
    .img{
        width: 60%;
        margin-left: 14%;
    }
    .fond{
        width: 50%;
        margin: 10px 5px;
    }
}
@media (min-width:768px){
    .cart{
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin: 15px;
    }
    .cart div h3{
        font-size: 1.8rem; 
        padding: 10px;
    }
    .cart div p{
        width: 75%;
        font-size: 1.1rem; 
        padding: 10px;
        margin: auto;
    }
    .fund{
        margin: 10px 10px;
        width: 50%;
    }
    .cart div{
        width: 98%;
    }
    .imge{
        width: 90%;
    }
    .img{
        width: 60%;
        margin-left: 14%;
    }
    .fond{
        width: 50%;
        margin: 10px 5px;
    }
}
@media (min-width:1024px){
    .cart{
        margin: 15px;
    }
    .cart div h3{
        font-size: 2rem; 
        padding: 10px;
    }
    .cart div p{
        font-size: 1.1rem; 
        padding: 10px;
    }
    .fund{
        margin: 10px 10px;
        width: 50%;
    }
    .cart div{
        width: 98%;
    }
    .imge{
        width: 90%;
    }
    .img{
        width: 60%;
        margin-left: 14%;
    }
    .fond{
        width: 50%;
        margin: 10px 5px;
    }

}